class ThreeJS extends Component {
  componentDidMount() {
    // === THREE.JS CODE START ===
    var rotationmodifier = 0.0001
    var scene = new THREE.Scene();
    var camera = new THREE.PerspectiveCamera(
      50,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );


   

    var renderer = new THREE.WebGLRenderer({ alpha: true });
    renderer.setClearColor(0xffffff, 0);
    renderer.shadowMap.enabled = true;
    renderer.shadowMapSoft = true;
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;

    renderer.setSize(window.innerWidth, window.innerHeight);
    document.body.appendChild(renderer.domElement);
    var geometry = new THREE.PlaneGeometry(200, 200, 200);
    var material = new THREE.ShadowMaterial({
      color: 0x666666,
      side: THREE.DoubleSide
    });
    material.opacity = 0.2;
    var plane = new THREE.Mesh(geometry, material);
    plane.position.z = -5;
    plane.receiveShadow = true;
    scene.add(plane);

    camera.position.z = 7.5;
    var loader = new GLTFLoader();

    var spotLight = new THREE.SpotLight(0xf5f5f5);
    spotLight.position.set(-0.5, -0.5, 10);
    spotLight.intensity = 1;

    spotLight.castShadow = true;

    const shadowmapmultiplier = 3;

    spotLight.shadow.mapSize.width = 1024 * 3;
    spotLight.shadow.mapSize.height = 1024 * 3;

    const d = 10;

    spotLight.shadow.camera.left = -d;
    spotLight.shadow.camera.right = d;
    spotLight.shadow.camera.top = d;
    spotLight.shadow.camera.bottom = -d;
    spotLight.shadow.radius = 3;
    scene.add(spotLight);

    var light2 = new THREE.AmbientLight(0xf5f5f5); // soft white light
    light2.intensity = 0.25;
    scene.add(light2);
    camera.add(light2)
    //scene.environment = new THREE.Color( 0x00ff00)



    var originalobjectsonlyyrotate = []
    var originalobjectsfullrotate = []

    var animatableobjectsfullrotate = []
    var animatableobjectsonlyyrotate = []

    loader.load(
      scenedata,
      function(gltf) {
        console.log('loaded scene')
        scene.add(gltf.scene);

        scene.traverse (function (object) {

          console.log(object.name)
        })

        gltf.scene.position.z = -1000

        var venus = scene.getObjectByName(
          "venus"
        );
        var chandelier = scene.getObjectByName("chandelier")
        var candelabra = scene.getObjectByName("Circle002")
        venus.castShadow = true;
        chandelier.castShadow = true;
        candelabra.castShadow = true;


//chandelier.material = material
//venus.material = material
        originalobjectsonlyyrotate.push(venus)
        originalobjectsfullrotate.push(chandelier)
        originalobjectsfullrotate.push(candelabra)

       // animatableobjects.push(venus)
      //  animatableobjects.push(chandelier)

        var randompositions = [-3, -3.2, -3.1,3, 3.2, 3.1]
        var randomypositions = [2, 3, 5]

        var window_focus = true;

window.onblur = function() { window_focus = false; }
window.onfocus = function() { window_focus = true; }
        //venus.material = new THREE.MeshPhysicalMaterial()
      function createNewFullRotate() {
    

        function canAdd() {
          if (animatableobjectsfullrotate.length > 0 ) {
            if (animatableobjectsfullrotate.slice(-1)[0].position.y > -8) {
              return true
            } else {
              return false
            }
          } else {
            return true
          }
        } 
        if (window_focus && canAdd()) {
        var object = originalobjectsfullrotate[Math.floor(Math.random()*originalobjectsfullrotate.length)];

          var objectclone = object.clone()
          scene.add(objectclone)
          objectclone.position.x = randompositions[Math.floor(Math.random()*randompositions.length)] //* (camera.aspect - 0.1)
          objectclone.position.y =  -8 - randomypositions[Math.floor(Math.random()*randomypositions.length)]
          animatableobjectsfullrotate.push(objectclone)
        }
          setTimeout(createNewFullRotate, 3000);
        
      }
      
      createNewFullRotate();

      var fullRotate = function() {
          requestAnimationFrame(fullRotate);
          //venus.rotation.z += 0.001;

          animatableobjectsfullrotate.forEach(function(object) {
            
             object.rotation.y += 0.005 + (rotationmodifier * 0.01)
             object.rotation.x += 0.001
             object.position.y += 0.005
             object.position.x += object.position.x/15000

             if (object.position.y > 10 ) {
               scene.remove(object)
             }
            })
          renderer.render(scene, camera);
        };
        fullRotate();
      },
      undefined,
      function(error) {
        console.error(error);
      }
    );
      var checkScrollSpeed = (function(settings){
        settings = settings || {};
    
        var lastPos, newPos, timer, delta, 
            delay = settings.delay || 50; // in "ms" (higher means lower fidelity )
    
        function clear() {
          lastPos = null;
          delta = 0;
        }
    
        clear();
    
        return function(){
          newPos = window.scrollY;
          if ( lastPos != null ){ // && newPos < maxScroll 
            delta = newPos -  lastPos;
          }
          lastPos = newPos;
          clearTimeout(timer);
          timer = setTimeout(clear, delay);
          return delta;
        };
    })();
    window.onscroll = function() {scrollCam()};

    function scrollCam() {
      var winScroll = document.body.scrollTop || document.documentElement.scrollTop;
      var height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
      var scrolled = (winScroll / height);
      camera.position.y = 0 - (5 * scrolled)
      spotLight.position.y = 0 - (5 * scrolled)
      rotationmodifier = Math.abs(checkScrollSpeed())
      //console.log (checkScrollSpeed())
    }

    var globalResizeTimer = null;

    $(window).resize(function() {
        if(globalResizeTimer != null) window.clearTimeout(globalResizeTimer);
        globalResizeTimer = window.setTimeout(function() {
         camera.aspect = window.innerWidth / window.innerHeight
         camera.updateProjectionMatrix();

         renderer.setSize( window.innerWidth, window.innerHeight );

        }, 200);
    });

    var scrollStop = function (callback) {

      // Make sure a valid callback was provided
      if (!callback || typeof callback !== 'function') return;
    
      // Setup scrolling variable
      var isScrolling;
    
      // Listen for scroll events
      window.addEventListener('scroll', function (event) {
    
        // Clear our timeout throughout the scroll
        window.clearTimeout(isScrolling);
    
        // Set a timeout to run after scrolling ends
        isScrolling = setTimeout(function() {
    
          // Run the callback
          callback();
    
        }, 66);
    
      }, false);
    
    };
    scrollStop(function () {
      rotationmodifier = 0;
  });


    // === THREE.JS EXAMPLE CODE END ===
  }
  render() {
    return <div ref={ref => (this.mount = ref)}> </div>;
  }
}

