class Form extends Component {
  constructor() {
    super();
    this.state = {
      title: '',
      link: '',
      linkvalid: 'background-transparent',
      type: '',
      description: '',
      buttonColor: "lightgray",
    }
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.radioChange = this.radioChange.bind(this);
    this.checkLink = this.checkLink.bind(this);


  }

  radioChange(e) {
    this.setState({
      type: e.currentTarget.value
    });
  }

  checkLink(e) {
      if (e.target.name == "link" && e.target.value != '') {
      if (validate(e.target.value)) {
        this.setState({
          linkvalid:'background-transparent'
        });
        console.log(this.linkvalid)

      } else {
        this.setState({
          linkvalid: 'background-red'
        });
        console.log(this.linkvalid)
      }
    }
  }

  handleChange(e) {

    this.setState({
      [e.target.name]: e.target.value
    });

    if (this.title != '' && this.type != "" && this.description != "") {
      this.setState({
        buttonColor: 'black'
      });
    }
  }

  handleSubmit(e) {
    e.preventDefault();
    if (this.title != '' && this.type != "" && this.description != "") {

      const itemsRef = firebase.database().ref('publicrecs');
      const item = {
        title: this.state.title,
        link: this.state.link,
        creator: "",
        rating: 0,
        description: this.state.description,
        type: this.state.type
      }
      itemsRef.push(item);
      this.setState({
        title: '',
        link: '',
        description:'',
      type:''     
     });
    }
  }

  render() {
    return (
      <section className="add-item">
  <form  onSubmit={this.handleSubmit}>
    
    <input type="text" name="title" placeholder="title" onChange={this.handleChange} value={this.state.title} />
    <input className={this.linkvalid} onBlur={this.checkLink}  type="text" name="link" placeholder="link" onChange={this.handleChange} value={this.state.link} />
    <div className="typeselector">
        {types.map(typeval => <div className="typeradio"><input id={typeval} checked={this.state.type === typeval} type="radio" key={typeval} value={typeval} onChange={this.radioChange.bind(this)}/><label for={typeval} class="radio" className="button">{typeval.toUpperCase()}</label></div>)} 
        </div> 
    <textarea name="description" placehold="Why do you like it?" onChange={this.handleChange} value={this.state.description} />
    <button>SUBMIT</button>
  </form>
</section>
    )
  }
}


